import appTabNav from "../../../components/basic-component/app-tab-nav/app-tab-nav.vue";

import { mapState } from "vuex";

export default {
    data() {
        return {
            tabList: [
                {id:1, name: '收入'},
                {id:2, name: '支出'}
            ],
            stepUser: {
                step_currency: 0
            },
            activeTab: 1,
            page: 2,
            list: [],
            setting: {
                currency_name: ''
            },
            over: false
        }
    },
    components: {
        "app-tab-nav": appTabNav,
    },
    computed: {
        ...mapState({
            userInfo: state => state.user.info,
            stepImg: state => state.mallConfig.plugin.step,
        })
    },
    methods: {
        tabStatus(e) {
            uni.showLoading({
                mask: true,
                title: '加载中...'
            });
            this.list = [];
            this.activeTab = e.currentTarget.dataset.id;
            this.getList();
        },
        getSetting() {
            let that = this;
            that.$request({
                url: that.$api.step.setting,
            }).then(response=>{
                that.getList();
                if(response.code == 0) {
                    that.setting = response.data;
                }else {
                    uni.showToast({
                        title: response.msg,
                        icon: 'none',
                        duration: 1000
                    });
                }
            }).catch(response => {
                that.$hideLoading();
            });
        },
        getList() {
            let that = this;
            that.$request({
                url: that.$api.step.log,
                data:{
                   type: that.activeTab
                },
            }).then(response=>{
                that.$hideLoading();
                uni.hideLoading();
                if(response.code == 0) {
                    that.list = response.data.list;
                    that.stepUser = response.data.stepUser;
                }else {
                    uni.showToast({
                        title: response.msg,
                        icon: 'none',
                        duration: 1000
                    });
                }
            }).catch(response => {
                that.$hideLoading();
                uni.hideLoading();
            });
        },
    },

    onLoad() { this.$commonLoad.onload();
        let that = this;
        that.$showLoading({
            type: 'global',
            text: '加载中...'
        });
        that.activeTab = 1;
        that.getSetting();
    }
}
